.Emulator-canvas {
  position: absolute;
  top: 48px;
  left: 0;
  width: 100vw;
  height: 90vw;
  background: -webkit-gradient(linear, left top, right top, from(#ffff00),to(#ff6d00));
  background: -webkit-linear-gradient(left, #ffff00 0%,#ff6d00 100%);
  background: -o-linear-gradient(left, #ffff00 0%,#ff6d00 100%);
  background: linear-gradient(to right, #ffff00 0%,#ff6d00 100%);;
  z-index: 0;
}

@media (orientation: landscape) {
  .Emulator-canvas {
    top: 0;
    left: calc(50% - 111.111111vh / 2);
    width: 111.111111vh;
    height: 100vh;
  }
}



